// ヘッダー
.ly_header {
  // padding-top: 10px;
  // padding-bottom: 10px;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: $cBoxShadow;
  background-color: $cWhite;
  &_inner {
    max-width: $contentsWidth_tab - $contentsPaddingWidth_tab * 2;
    margin-right: auto;
    margin-left: auto;
  }
}
@include mq("tab") {
  .ly_header {
    border-bottom: 1px solid $cGrey;
  }
}

