// ヘッダーナビゲーション
.ly_navigation {
  display: none;
}
@include mq("tab") {
  .ly_navigation {
    display: block;
    position: sticky;
    top: 0;
    z-index: 1000;
    height: 64px;
    box-shadow: 0 8px 8px -8px rgba(63, 45, 45, .267);
    background-color: $cWhite;

    &_inner {
      max-width: $contentsWidth_tab;
      height: 100%;
      margin: 0 auto;
      padding: 0 $contentsPaddingWidth_tab;
    }
  }
}

// ヘッダーナビゲーション
@include mq("tab") {
  .bl_headerNav {
    display: flex;
    height: 100%;

    &_item {
      display: flex;
      flex-grow: 1;
      position: relative;
      transition: $transition;

      &::before {
        display: block;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(-50%, -50%);
        width: 1px;
        height: 35px;
        background-color: #b8b9dc;
        content: "";
      }
      &:last-child::after {
        display: block;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(50%,-50%);
        width: 1px;
        height: 35px;
        background-color: #b8b9dc;
        content: "";
      }
    }
    &_link {
      display: flex;
      flex-grow: 1;
      justify-content: center;
      padding: 0 10px;
      color: inherit;
      text-decoration: none;
      transition: $transition;
      &:hover {
        color: $cOrange;
      }
      &:hover .bl_headerNav_txt::after {
        left: 0;
        width: 100%;
      }
    }
    &_txt {
      display: flex;
      align-items: center;
      position: relative;
      font-weight: bold;
      line-height: 1;
      white-space: nowrap;
      &::after {
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 0;
        height: 4px;
        border-radius: 5px;
        background-color: $cOrange;
        content: "";
        transition: .15s;
      }
    }
  }
}
